<template>
    <div class="reservation">
        <div class="top-menu">
            <div class="search-item right">
                <span>{{ $L('预约日期') }}：</span>
                <el-date-picker class="time-search"
                                style="width: 300px;"
                                v-model="SearchTimeZone"
                                type="daterange"
                                align="right"
                                size="small"
                                :start-placeholder="$L('开始日期')"
                                :end-placeholder="$L('结束日期')"
                                value-format="yyyy-MM-dd"
                                @change="SearchTimeFunc">
                </el-date-picker>
                <el-select class="search" v-model="messageType" size="small"
                           @change="changeTypeFunc">
                    <el-option
                            v-for="item in typeList"
                            :key="item.type"
                            :label="item.name"
                            :value="item.type">
                    </el-option>
                </el-select>
                <el-input :placeholder="$L('输入预约订单/订单号')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
        </div>
        <div class="reservation-content" v-loading="loading" v-if="tableData && tableData.hasOwnProperty('list') && tableData.list.length > 0">
            <div class="list-main" v-for="(item,key) in tableData.list" :key="key">
                <div class="list-msg">
                    <span class="inner-span">{{$L('订单编号')}}：{{ item.orderSn }}</span>
                    <span class="order-time">{{$L('下单时间')}}：{{ item.addTime }}</span>
                </div>
                <div class="list-head">
                    <div class="item item-two">{{$L('预约订单')}}</div>
                    <div class="item item-one">{{$L('价格')}}</div>
                    <div class="item item-one">{{$L('预付金额')}}</div>
                    <div class="item item-one">{{$L('预约日期')}}</div>
                    <div class="item item-one">{{$L('订单状态')}}</div>
                    <div class="item item-three">{{$L('操作')}}</div>
                </div>
                <div class="list-info">
                    <div class="item item-two">
                        <div class="info">
                            <div class="list-main-left">
                                <img :src="item.productPic" alt="">
                            </div>
                            <div class="list-main-right">
                                <p class="order-info">{{item.proName}}</p>
                            </div>
                        </div>
                    </div>
                    <div class="item item-one">
                        <div class="price font-grey">{{ item.orderAmount }}</div>
                    </div>
                    <div class="item item-one">
                        <div class="price font-red">{{ item.moneyPaid }}</div>
                    </div>
                    <div class="item item-one">
                        <div class="font">
                            {{ item.bookTime }}<br>
                            <span v-if="item.timeSpec">{{ item.timeSpec }}</span>
                        </div>
                    </div>
                    <div class="item item-one">
                        <div v-if="item.order_status == 1" class="font font-red">{{$L('待处理')}}</div>
                        <div v-else-if="item.order_status == 2" class="font">{{$L('已消费')}}</div>
                        <div v-else-if="item.order_status == 3" class="font">{{$L('已取消预约')}}</div>
                        <div v-else-if="item.order_status == 4 && item.comments_status === 0" class="font">{{$L('待评价')}}</div>
                        <div v-else-if="item.order_status == 4 && item.comments_status === 1" class="font">{{$L('已评价')}}</div>
                        <div v-else-if="item.order_status == 5" class="font">{{$L('已逾期')}}</div>
                        <div v-else class="font">{{$L('未知状态')}}</div>
                        <el-button type="text" @click="getInfoFunc(item.id)">{{$L('订单详情')}}</el-button>
                    </div>
                    <div class="item item-three">
                        <el-button type="primary" v-if="item.order_status === 1 && item.pay_status === 0" @click="changeOrderType(item,'pay')">{{$L('立即支付')}}</el-button>
                        <el-button type="default" v-if="item.order_status === 1"  @click="changeOrderType(item,'cancel')">{{$L('取消预约')}}</el-button>
                        <el-button type="default" v-else-if="item.order_status >= 3"  @click="changeOrderType(item,'del')">{{$L('删除订单')}}</el-button>
                        <el-button type="primary" v-if="item.order_status >= 3 && item.comments_status === 0"  @click="changeOrderType(item,'evaluate')">{{$L('去评价')}}</el-button>
                        <el-button type="primary" v-if="item.order_status >= 3 && item.comments_status === 1"  @click="changeOrderType(item,'evaluateInfo')">{{$L('查看评价')}}</el-button>
                    </div>
                </div>
            </div>
            <div class="list-pages" v-if="tableData.allNum>pageSize">
                <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10,20,30]"
                        :page-size="pageSize"
                        layout="prev, pager, next, jumper"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
        <div class="tips" v-else v-loading="loading">
            <p class="no-list">{{ $L('无预约信息') }}</p>
        </div>
    </div>
</template>

<script>
    import orderPay from "../orderChildren/orderPay";
    export default {
        name: "reservation",
        components: {orderPay},
        data () {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                SearchTimeZone:'',
                messageType:'all',
                typeList:[
                    {
                        type:'all',
                        name:this.$L('全部时间')
                    },
                    {
                        type:'seven',
                        name:this.$L('近7天订单')
                    },
                    {
                        type:'thirty',
                        name:this.$L('近30天订单')
                    },
                    {
                        type:'halfYear',
                        name:this.$L('近半年订单')
                    },
                    {
                        type:'year',
                        name:this.$L('近1年订单')
                    },
                    {
                        type:'yearAgo',
                        name:this.$L('1年以前订单')
                    }
                ],
                currentPage:1,
                pageSize:10,
                dtCreateStart:'',
                dtCreateEnd:'',
                timeScreening:'all',
                inputValue:'',
                tableData:{},
                type:'all'
            }
        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`

            this.getData(to);
            next();
        },
        created() {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                if(!router){
                    router = _self.$route;
                };
                _self.loading = true;
                let arr = {
                    searchStatus : router.params.type ? router.params.type :'all',
                    p: _self.currentPage,
                    num: _self.pageSize,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd,
                    timeScreening: _self.messageType,
                    keyword: _self.inputValue
                }
                _self.$api('getReservationList', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        // _self.theadName = reData.data.group;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            backFunc() {
                this.$router.back(-1);
            },
            SearchTimeFunc(time) {
                if (time !== null) {
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                } else {
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            searchFunc(val) {
                this.inputValue = val;
                this.getData();
            },
            changeTypeFunc(val) {
                this.messageType = val;
                this.getData();
            },
            handleSizeChange(val){
                console.log(val);
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            changeOrderType(item, status) {
                switch (status) {
                    case 'pay':
                        //立即付款
                        if(item.order_status === 1 || item.pay_status === 0){
                            this.$store.commit('setDialogObj', {
                                name: this.$L('订单支付'),
                                view: orderPay,
                                width: '900px',
                                loading: true,
                                data:{
                                    orderId: item.orderId,
                                }
                            });
                        }
                        break;
                    case 'cancel':
                        //取消订单
                        if (item.order_status === 1) {
                            this.$confirm(this.$L('确认取消订单吗？'), this.$L('取消订单'), {
                                confirmButtonText: this.$L('立即取消'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('cancleReservation', {
                                    orderId: item.orderId,
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.getData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'del':
                        //删除订单
                        if (item.order_status >= 3) {
                            this.$confirm(this.$L('是否确认删除订单？'), this.$L('删除订单'), {
                                confirmButtonText: this.$L('立即删除'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('delReservation', {
                                    orderId: item.orderId,
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.getData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'evaluate':
                        //去评价
                        if (item.order_status >= 3 && item.comments_status === 0) {
                            this.$router.push({name: "resOrderComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'evaluateInfo':
                        //去评价
                        if (item.order_status >= 3 && item.comments_status === 1) {
                            this.$router.push({name: "resCheckComment", params: {id: item.orderId}});
                        }
                        break;
                }
            },
            showLoad(type) {
                if (typeof type === "undefined" || type) {
                    this.loading = this.$loading({lock: true});
                } else {
                    if (this.loading) {
                        this.loading.close();
                        this.loading = null;
                    }
                }
            },
            getInfoFunc(id){
                this.$router.push({name:'reservationInfo',params:{id:id}});
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common.scss";
    @import "../../../assets/css/common/lib";
    .reservation{
        width: 100%;
        .top-menu{
            height: 32px;
            .left{
                width: 33%;
                display: inline-block;
                float: left;
                margin-left: 20px;
                .item{
                    width: 90px !important;
                }
            }
            .right{
                display: inline-block;
                float: right;
                .time-search{
                    vertical-align: middle;
                    line-height: normal;
                    margin-bottom: 1px;
                }
                .font{
                    padding-left: 20px;
                    font-size: 16px;
                }
                .search{
                    width: 130px;
                    padding-left: 10px;
                }
                .input-with-select{
                    width: 250px;
                    padding-left: 10px;
                }

            }
        }
        .reservation-content{
            margin-top: 10px;
            .list-main{
                background-color: #fff;
                padding: 15px 0;
                margin-bottom: 15px;
                .list-msg{
                    background: #fff;
                    font-size: 14px;
                    .inner-span{
                        padding: 0 40px 0 20px;
                        height: 50px;
                        line-height: 50px;
                    }
                    .order-time{
                        color: #999;
                    }
                }
                .list-head{
                    display: flex;
                    margin: 0 20px;
                    border: 1px solid #dcdfe6;
                    .item{
                        height: 50px;
                        line-height: 50px;
                        font-size: 14px;
                        text-align: center;
                        background: #fff;
                        color: #666;
                        font-weight: 700;
                        padding: 0 20px;
                        &.item-one{
                            width: 13%;
                        }
                        &.item-two{
                            width: 35%;
                            text-align: left;
                        }
                        &.item-three{
                            font-size: 14px;
                            width: 13%;
                        }
                    }
                }
                .list-info{
                    margin: 0 20px;
                    background: #fff;
                    padding: 20px 0;
                    .item {
                        display: inline-block;
                        text-align: center;
                        vertical-align: top;
                        &.item-one{
                            width: 13%;
                            height: 100px;
                            font-size: 14px;
                            .price{
                                height: 100px;
                                margin-bottom: 20px;
                                padding-top: 10px;
                                &:last-child{
                                    margin-bottom: 0;
                                    font-weight: 700;
                                }
                                .font-grey{
                                    color: #999;
                                    text-decoration: line-through;
                                }
                            }
                            .font-red{
                                color: red;
                                padding: 10px 0;
                            }
                            .font{
                                padding: 10px 0;
                            }
                            .unconfirm{
                                color: green;
                                padding: 10px 0;
                            }
                            .undeliver{
                                color: red;
                                padding: 10px 0;
                            }
                        }
                        &.item-two{
                            width: 35%;
                            text-align: left;
                        }
                        &.item-three{
                            width: 13%;
                            font-size: 14px;
                            padding: 10px 0;
                            button{
                                display: block;
                                margin: 0 auto 10px;
                                display: block;
                                width: 100px;
                                line-height: 35px;
                                padding: 0;
                                &:last-child{
                                    margin-bottom: 0;
                                }
                            }
                        }
                        .info{
                            position: relative;
                            width: 100%;
                            overflow: hidden;
                            padding-bottom: 20px;
                            &:last-child{
                                padding-bottom: 0;
                            }
                            .list-main-left{
                                width: 100px;
                                height: 100px;
                                float: left;
                                img{
                                    width: 100px;
                                    height: 100px;
                                }
                            }
                            .list-main-right{
                                padding-left: 18px;
                                float: left;
                                height: 100px;
                                width: calc(100% - 120px);
                                p{
                                    line-height: 26px;
                                }
                                .order-info{
                                    cursor: pointer;
                                    overflow:hidden;
                                    display:-webkit-box;
                                    text-overflow:ellipsis;
                                    -webkit-line-clamp:2;
                                    -webkit-box-orient:vertical;
                                    white-space:normal;
                                    font-size: 14px;
                                }
                            }

                        }

                    }
                }
            }
            .list-pages{
                text-align: center;
                padding: 20px 0 20px;
            }
        }
        .tips{
            position: relative;
            background: #fefefe;
            padding: 20px;
            margin-top: 10px;
            text-align: center;
            .no-list{
                @extend .no-select;
                position: relative;
                width: 100%;
                height: 380px;
                line-height: 380px;
                text-align: center;
                background-color: #ffffff;
                font-size: 16px;
                color: #999;
                margin-bottom: 20px;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }

</style>