<template>
    <div class="order-pay" v-loading="loading">
        <div class="order-main" v-if="!gotoPay">
            <div class="order-detail">
                <div class="l-box">
                    <i class="bbx-font bbx-icon-zhifu1"></i>
                </div>
                <div class="r-box">
                    <div class="order-tips">
                        <div class="title">{{$L('订单编号')+'：'+(routerData.hasOwnProperty('orderInfo') &&
                            routerData.orderInfo.hasOwnProperty('orderSn')?routerData.orderInfo.orderSn:"")}}
                        </div>
                        <div class="n">{{$L('订单已提交，请尽快完成支付')}}</div>
                    </div>
                    <div class="order-list">
                        <ul>
                            <li>{{$L('订单金额：')}}<span style="color: #FF0000">{{routerData.hasOwnProperty('orderInfo') && routerData.orderInfo.hasOwnProperty('orderAmount')?routerData.orderInfo.orderAmount:""}}</span>
                            </li>
                            <li>{{$L('配送地址：')+(routerData.hasOwnProperty('orderInfo') &&
                                routerData.orderInfo.hasOwnProperty('address') && routerData.orderInfo.address ?routerData.orderInfo.address:"")}}
                            </li>
                            <li>{{$L('联系电话：')+(routerData.hasOwnProperty('orderInfo') &&
                                routerData.orderInfo.hasOwnProperty('mobile') && routerData.orderInfo.address ?routerData.orderInfo.mobile:"")}}
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="pay-type">
                <div class="pay-title">{{$L('选择支付方式')}}</div>
                <div class="pay-box">
                    <ul class="r-pay" v-if="routerData.hasOwnProperty('payListArr')">
                        <li class="li" v-for="(item,index) in routerData.payListArr" :key="index">
                            <el-radio-group v-model="payId">
                                <el-radio :label="item.payid">
                                    <img :src="item.logo" alt="" class="img">
                                </el-radio>
                            </el-radio-group>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="bottom">
                <el-button size="medium" @click="closeFunc">{{$L('关闭')}}</el-button>
                <el-button size="medium" @click="orderInfo">{{$L('查看订单详情')}}</el-button>
                <el-button type="primary" size="medium" @click="payOrder">{{$L('立即支付')}}</el-button>
            </div>
        </div>
        <iframe v-if="gotoPay" :src="payUrl" class="windows" ref="ifr" @load="onLoad" :style="{height:height}"></iframe>
    </div>
</template>

<script>
    export default {
        name: "orderPay",
        inject: ['reload'],
        props: {
            height: {
                type: String,
                default: '',
            },
            loading: {
                type: Boolean,
                default: false
            },
            data: {
                type: Object,
                default: () => {
                    return {};
                }
            },
            listener: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        watch: {
            listener(newData, oldData) {
                if (newData && newData.hasOwnProperty('order')) {
                    this.payListen(newData.order, newData.data);
                }
            }
        },
        data() {
            return {
                payId: 0,
                routerData: {},
                gotoPay: false,
            }
        },
        computed: {
            payUrl: {
                get() {
                    return this.$C('serverUrlAjax') + "/exusers/u_payin_finish.php?order_id=" + this.data.orderId + "&pay_id=" + this.payId + "&vue=1&lang=" + this.$langID();
                }
            }
        },
        created() {
            this.getData();
        },
        methods: {
            payListen(action, data) {
                switch (action) {
                    case 'showError':
                        if (data.hasOwnProperty('msg')) {
                            this.$alert(data.msg, data.hasOwnProperty('title') ? data.title : this.$L("支付失败"), {
                                confirmButtonText: this.$L('确定'),
                                type: "error",
                                showClose: false
                            }).then(() => {
                                this.closeLoading();
                                this.gotoPay = false;
                            });
                        }
                        break;
                    case 'gotoOrderInfo':
                        this.orderInfo();
                        if (data.hasOwnProperty('msg') && data.msg && data.msg !== '') {
                            this.$message({
                                message: data.msg,
                                type: 'success'
                            });
                        }
                        break;
                    case 'payInfo':
                        if (data.hasOwnProperty('info')) {
                            this.$alert(data.info, this.$L('支付提示'), {
                                confirmButtonText: this.$L('确定'),
                                type: "warning",
                                showClose: false
                            }).then(() => {
                                this.orderInfo();
                            });
                        }
                        break;
                    case 'getGiftCard':

                        if (data.hasOwnProperty('msg') && data.msg && data.msg !== '' && this.$cookies.isKey('userCardToken') == false) {
                            this.$confirm(data.msg, this.$L('提示'), {
                                confirmButtonText: this.$L('确定'),
                                cancelButtonText: this.$L('不在提示'),
                                type: 'warning'
                            }).then(() => {
                                this.$emit('closeDialog');
                                this.$router.push({name: 'userCard'});
                            }).catch(action => {
                                if (action == 'cancel') {
                                    this.$cookies.set("userCardToken","userCard",'3y');
                                }
                                this.$emit('closeDialog');
                                this.reload();

                            });
                        } else {
                            this.$emit('closeDialog');
                            this.$message({
                                message: this.$L('订单支付成功'),
                                type: 'success'
                            });
                            this.reload();
                        }

                        break;
                }
            },
            onLoad() {
                this.$refs.ifr.contentWindow.postMessage({msg: "isVue"}, this.$C('serverUrlAjax'));
            },
            payOrder() {
                if (this.payId === 0) {
                    this.$message.error('请选择支付方式');
                } else {
                    this.showLoading();
                    this.gotoPay = true;
                }
            },
            orderInfo() {
                this.closeFunc();
                if (this.$route.name === 'orderInfo' && this.$route.params.id === this.data.orderId) {
                    setTimeout(() => {
                        this.reload();
                    }, 500);
                } else {
                    this.$router.push({name: 'orderInfo', params: {id: this.data.orderId}});
                }
            },
            showLoading() {
                this.$emit('showDialogLoading');
            },
            closeLoading() {
                this.$emit('closeDialogLoading');
            },
            closeFunc() {
                this.$emit('closeDialog');
            },
            getData() {
                const _self = this;
                if (!_self.data.hasOwnProperty('orderId')) {
                    _self.$confirm(_self.$L('参数有误'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('关闭窗口'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.closeFunc();
                    });
                } else {
                    _self.$api('payOrder', {orderId: _self.data.orderId}, function (reData) {
                        if (reData.flag === 1) {
                            _self.routerData = reData.data;
							console.log('_self.routerData',_self.routerData)
                            _self.closeLoading();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('重试'),
                                cancelButtonText: _self.$L('关闭'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.closeFunc();
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击'),
                            cancelButtonText: _self.$L('关闭'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.closeFunc();
                        });
                    });
                }
            }
        }
    }
</script>

<style lang="scss" scoped>

    .order-pay {
        position: relative;
        width: 100%;

        .order-main {
            position: relative;
            width: 100%;

            .order-detail {
                position: relative;
                background-color: #fcfcfc;
                overflow: hidden;
                height: 230px;

                .l-box {
                    position: absolute;
                    width: 200px;
                    height: 100%;
                    text-align: center;
                    line-height: 230px;

                    .bbx-font {
                        font-size: 100px;
                        color: #83BD39;
                    }
                }

                .r-box {
                    position: absolute;
                    width: calc(100% - 200px);
                    height: 100%;
                    overflow: hidden;
                    left: 200px;

                    .order-tips {
                        padding: 20px 15px;

                        .title {
                            font-size: 26px;
                            font-weight: normal;
                            line-height: 180%;
                        }

                        .n {
                            color: #999;
                            font-size: 12px;
                        }
                    }

                    .order-list {
                        line-height: 28px;
                        color: #666;
                        padding-left: 15px;
                    }
                }
            }

            .pay-type {
                position: relative;
                width: 100%;
                padding: 0 20px;

                .pay-title {
                    position: relative;
                    font-size: 14px;
                    line-height: 40px;
                }

                .pay-box {
                    position: relative;
                    border: 1px solid #DBDBDB;
                    overflow: hidden;

                    .r-pay {
                        padding: 15px 0 0 15px;

                        .li {
                            margin-right: 15px;
                            float: left;
                            margin-bottom: 15px;

                            .el-radio {
                                cursor: pointer;

                                .img {
                                    border: 1px solid #DBDBDB;
                                    width: 102px;
                                    height: 42px;
                                    vertical-align: middle;
                                }
                            }
                        }
                    }
                }
            }

            .bottom {
                text-align: right;
                padding: 20px;
                background: #f5f5f5;
                border: 1px solid #ddd;
                position: relative;
                width: 100%;
                margin-top: 20px;
            }
        }

        .windows {
            position: relative;
            width: 100%;
            min-height: 500px;
        }
    }
</style>